.feedback-form {
  padding: 50px;
  max-width: 403px;
  background: #011C40;
  box-shadow: 0px 4px 45px rgba(1, 28, 64, 0.15);
}

.feedback-form h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  display: flex;
  align-items: center;
  color: #F9FBFD;
}

.contacts {
  padding: 0 var(--side-offset, 20px);
  padding-top: 120px;
  padding-bottom: 120px;
}

.contacts__row {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.contacts-description {
  max-width: 500px;
}

.contacts-description h3 {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  display: flex;
  align-items: center;
  color: #011C40;
}

.contacts-description p {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  display: flex;
  align-items: center;
  color: #011C40;
}

.footer {
  padding: 0 var(--side-offset, 20px);
  padding-top: 50px;
  padding-bottom: 50px;
  background: #011C40;
}

.footer__row {
  display: flex;
  align-items: center;
}

.footer-logo {
  margin-right: 40px;
}

.footer-logo__icon {
  display: block;
}

.socials {
  margin-left: auto;
}

.socials__list {
  margin: 0;
  padding: 0;
  display: flex;
  width: 106px;
  justify-content: space-between;
  list-style-type: none;
}

